.play-button {
   position: absolute;
   top: 50%;
   left: 50%;
   transform: translate(-50%, -50%);
   --size: 6.4rem;
   width: var(--size);
   height: var(--size);
   display: flex;
   align-items: center;
   justify-content: center;
   border-radius: 50%;
   overflow: hidden;
   z-index: 2;
   cursor: pointer;
   transition: .3s ease-in-out;
}

.news-carousel__slide .play-button {
   --size: 4.6rem;
}

.play-button:hover {
   opacity: .7;
   transition: .3s ease-in-out;
}

.play-button::before {
   display: block;
   content: '';
   position: absolute;
   top: 0;
   left: 0;
   width: 100%;
   height: 100%;
   background: #383838;
   opacity: 0.9;
   transition: .3s ease-in-out;
}


.play-button img,
.play-button svg {
   width: 1.5rem;
   position: absolute;
   z-index: 2;
   height: auto;
   top: 50%;
   left: 53%;
   transform: translate(-50%, -50%);
}