.main {
   margin-top: 6rem;
   margin-bottom: 8rem;
   min-height: 40vh;

   @media screen and (max-width: 1450px) {
      margin-bottom: 4rem;
   }
}

.main-sidebar {
   @media screen and (max-width: 1500px) {

      .articles,
      .rek-sidebar,
      .brand {
         display: none;
      }
   }
}

.main__container {
   display: grid;
   grid-template-columns: 74.662162162% auto;
   column-gap: 10rem;

   @media (max-width: 1500px) {
      column-gap: 5rem;
      display: block;
   }

}

.main__container-edit {
   display: grid;
   grid-template-columns: 35rem auto;
   column-gap: 10rem;

   @media (max-width: 1500px) {
      column-gap: 5rem;
   }

   @media (max-width: 1350px) {
      grid-template-columns: 28rem auto;
   }

   @media (max-width: $tablet) {
      grid-template-columns: 25rem auto;
      column-gap: 3rem;
   }

}

.main__container-news {
   display: grid;
   grid-template-columns: auto 74.662162162%;
   column-gap: 10rem;

   @media (max-width: 1500px) {
      display: block;
   }

   @media (max-width: 1350px) {
      margin-bottom: 8rem;
   }

   @media (max-width: $tablet) {
      margin-bottom: 0;
   }

}

.main__container-catalog {
   display: grid;
   grid-template-columns: auto 74.662162162%;
   column-gap: 10rem;

   @media (max-width: 1500px) {
      display: block;
   }

}

.page-heading {
   margin-bottom: 4rem;
}

.main-sub-menu-top {
   margin-top: 12rem;
}